import SignUp from "./components/js/SignUp";
import Login from "./components/js/login";
import ErrorPage from "./components/js/ErrorPage";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import Home from "./components/js/Home";
import Connected from "./components/js/Connected";
import Screen6 from "./components/js/Screen6";
import Screen9 from "./components/js/Screen9";

function App() {
  return (
    <Router>
      <Switch>
        <Route path="/" exact component={Home} />
        <Route path="/pushOrFold" component={Home} />
        <Route path="/connected" component={Connected} />
        <Route path="/9" component={Screen9} />
        <Route path="/6" Component={Screen6} />
        <Route path="/signup" component={SignUp} />
        <Route path="/login" component={Login} />
        <Route path="/error" component={ErrorPage} />
      </Switch>
    </Router>
  );
}

export default App;
